<template>
  <div>
    <el-row>
      <el-col :span="12">
        <div class="operatetips">
          <!--存储和物料统计  -->
          <!-- <div class="feeder_statistical" v-if="beforeShow">
						<div class="feeder_total feeder_num">
							<span>总计:</span>
							<span>46</span>
						</div>
						<div class="feeder_sended feeder_num">
							<span>已发:</span>
							<span>6</span>
						</div>
						<div class="feeder_error feeder_num">
							<span>缺失:</span>
							<span>0</span>
						</div>
						<div class="feeder_waiting feeder_num">
							<span>待发:</span>
							<span>40</span>
						</div>
					</div> -->
          <!-- 物料展示列表 -->
          <div v-if="beforeShow">
            <template>
              <el-table :header-cell-style="{background:'#DFEBF9',color:'#606266'}" :data="datalist" height="520"
                :empty-text="$t('common.nodata')" :row-class-name="tableRowClassName" border>
                <el-table-column prop="serial_num" :label="$t('common.No')" width="50">
                </el-table-column>
                <el-table-column prop="master_pn" :show-overflow-tooltip='true' :label="$t('storage.label_code')">
                </el-table-column>
                <el-table-column prop="feeder_name" :label="$t('storage.warehouse_name')">
                </el-table-column>
              </el-table>
            </template>
          </div>
          <!-- 操作提示 -->
          <div class="tipdiv" v-if="!beforeShow">
            <img src="../../assets/images/tip.png" alt="" class="tipimg">
            <div class="tipword">
              {{$t('feeder.optips')}}
            </div>
          </div>
        </div>
      </el-col>
      <el-col :span="12">
        <div class="feederinfo">
          <div class="tipdiv doingtipdiv">
            <img src="../../assets/images/doing.png" alt="" class="tipimg">
            <div class="tipword">
              <p>{{$t('feeder.now_mate')}}</p>
              <p class="showmate_p">-{{now_mate.master_pn}}</p>
              <p class="showmate_p">-{{now_mate.feeder_name}}</p>
            </div>
          </div>
          <div class="tipdiv lasttipdiv">
            <img src="../../assets/images/last.png" alt="" class="tipimg">
            <div class="tipword">
              <p>{{$t('feeder.before_mate')}}</p>
              <p class="showmate_p">-{{before_mate.master_pn}}</p>
              <p class="showmate_p">-{{before_mate.feeder_name}}</p>
            </div>
          </div>
          <div class="tipdiv errtipdiv">
            <img src="../../assets/images/error.png" alt="" class="tipimg">
            <div class="tipword">
              <p>{{$t('feeder.lack_mate')}}</p>
              <p class="showmate_p">-{{lack_mate.master_pn}}</p>
              <p class="showmate_p">-{{lack_mate.feeder_name}}</p>
            </div>
          </div>
          <div class="tipdiv ">
            <div class="tipword">
              <!-- 站位控制 -->
              <div class="feeder_ctrl">
                <span class="feeder_title">{{$t("feeder.shelf_id")}}</span>
                <el-select value-key="item" v-model="shelf_id">
                  <el-option v-for="item in shelfList" :key="item" :label="item" :value="item">
                  </el-option>
                </el-select>
              </div>
              <div class="feeder_ctrl">
                <span class="feeder_title">{{$t('feeder.destination')}}</span>
                <el-input v-model="destination" clearable></el-input>
              </div>
              <div class="feeder_ctrl">
                <span class="feeder_title">{{$t('feeder.order_id')}}</span>
                <el-input v-model="order_id" clearable></el-input>
              </div>
              <p class="controltip" v-show="inputneeded">{{$t('feeder.controltip')}}</p>
              <div class="feeder_ctrl">
                <el-button type="primary" @click="search">{{$t('common.search')}}</el-button>
                <el-button type="warning" @click="send">{{$t('feeder.send')}}</el-button>
              </div>
            </div>
          </div>
        </div>
      </el-col>
    </el-row>
  </div>
</template>
<script>
  import {
    onlineShelfList
  } from '@/request/api/materialshelf'
  import {
    sendMateByFeeder,
    feederOut
  } from '@/request/api/order'
  export default {
    data() {
      return {
        shelfList: [],
        shelf_id: '',
        destination: '',
        order_id: '',
        beforeShow: false,
        inputneeded: false,
        datalist: [],
        lack_mate: '',
        now_mate: '',
        before_mate: '',
        timer: null,
        flg: 0,
      }
    },
    watch: {
      flg(val) {
        if (val) {
          clearInterval(this.timer); //关闭
        }
      }
    },
    mounted() {
      this.getShelfList()
    },
    beforeDestroy() {
      if (this.timer) { //如果定时器还在运行 或者直接关闭，不用判断
        clearInterval(this.timer); //关闭
      }
    },
    methods: {
      getShelfList() {
        onlineShelfList({}).then(res => {
          if (res.result) {
            this.shelfList = res.data
          } else {
            this.$message({
              showClose: true,
              message: res.msg,
              type: 'error'
            })
          }
        })
      },
      search() {
        if (this.shelf_id && this.destination && this.order_id) {
          this.inputneeded = false
          sendMateByFeeder({
            shelf_id: this.shelf_id,
            destination: this.destination,
            order_id: this.order_id,
            send_type: 0
          }).then(res => {
            if (res.result) {
              this.beforeShow = true
              this.datalist = res.data
            } else {
              this.$message({
                showClose: true,
                message: res.msg,
                type: 'error'
              })
            }
          })
        } else {
          this.inputneeded = true
        }

      },
      send() {
        if (this.shelf_id && this.destination && this.order_id) {
          this.inputneeded = false
          sendMateByFeeder({
            shelf_id: this.shelf_id,
            destination: this.destination,
            order_id: this.order_id,
            send_type: 1
          }).then(res => {
            if (res.result) {
              this.beforeShow = true
              this.timer = window.setInterval(() => {
                this.getFeederOut()
              }, 2000)
            } else {
              this.$message({
                showClose: true,
                message: res.msg,
                type: 'error'
              })
            }
          })
        } else {
          this.inputneeded = true
        }
      },
      getFeederOut() {
        feederOut({
          shelf_id: this.shelf_id,
          destination: this.destination,
          order_id: this.order_id
        }).then(res => {
          if (res.result) {
            this.flg = res.flg
            this.datalist = res.mate_list
            this.before_mate = res.before_mate
            this.now_mate = res.now_mate
            this.lack_mate = res.lack_mate
          } else {
            this.$message({
              showClose: true,
              message: res.msg,
              type: 'error'
            })
          }
        })
      },
      tableRowClassName({
        row,
        rowIndex
      }) {
        if (row.mate_state == 1) {
          return 'nowmate'
        } else if (row.mate_state == 2) {
          return 'lackmate'
        } else if (row.mate_state == 3) {
          return 'beforemate'
        }
        return ''

      }
    }
  }
</script>
<style>
  .operatetips,
  .feederinfo {
    border: 1px solid #999999;
    margin: 2%;
    border-radius: 6px;
    background: #f3f3f3;
    padding: 20px;
    font-size: 18px;
    color: #333333;
  }

  .tipdiv {
    display: flex;
    padding: 10px 20px;
    background: rgba(0, 123, 255, 0.1);
    border-radius: 6px;
    border: 1px solid rgb(0, 123, 255);
    margin-bottom: 20px;
  }

  .tipdiv:last-child {
    margin-bottom: 0px;
  }

  .feeder_statistical {
    padding: 10px 20px;
    border: 1px solid rgb(0, 123, 255);
    border-radius: 6px;
    margin-bottom: 20px;
    display: flex;
    align-content: flex-center;
    align-items: center;
    justify-content: space-around;
  }

  .feeder_num {
    padding: 10px 20px;
    border-radius: 6px;
    color: white;
  }

  .feeder_total {
    background: #409EFF;
  }

  .feeder_sended {
    background: rgb(191, 191, 191);
  }

  .feeder_error {
    background: rgb(216, 30, 6);
  }

  .feeder_waiting {
    background-color: #E6A23C;
  }

  .tipimg {
    width: 32px;
    height: 32px;
  }

  .tipword {
    text-align: left;
    margin-left: 20px;
  }

  .lasttipdiv {
    background: rgba(26, 250, 41, 0.1);
    border: 1px solid rgb(26, 250, 41);

    /*margin-bottom:20px;*/
  }

  .doingtipdiv {
    background: rgba(255, 165, 0, 0.2);
    border: 1px solid rgb(191, 191, 191);
    /*margin-bottom:20px;*/
  }

  .errtipdiv {
    background: rgba(216, 30, 6, 0.1);
    border: 1px solid rgb(216, 30, 6);
    /*margin-bottom: 20px;*/
  }

  .feeder_ctrl {
    display: flex;
    height: 50px;
    line-height: 50px;
  }

  .feeder_ctrl .el-input {
    width: 240px;
  }

  .feeder_ctrl .el-input__inner {
    width: 240px;
  }

  .feeder_title {
    display: block;
    width: 120px;
  }

  .feeder_ctrl .el-button {
    width: 120px;
    margin-right: 60px;
  }

  .showmate_p {
    font-size: 16px;
    height: 28px;
    line-height: 28px;
  }

  .el-table .beforemate {
    background: rgba(26, 250, 41, 0.1);
  }

  .el-table .nowmate {
    background: rgba(255, 165, 0, 0.2);
  }

  .el-table .lackmate {
    background: rgba(216, 30, 6, 0.1);
  }

  .controltip {
    font-size: 12px;
    color: red;
    height: 24px;
    line-height: 24px;
  }
</style>



